.app.dark {
    background-color: #222;
    color: rgb(156, 156, 156);

    .chartGrid {
        stroke: rgba(228, 228, 228, 0.219);
    }

    .table {
        background-color: #222;

        .tableCell {
            color: gray;
        }
    }

    .navbar {
        color: #999;
        border-color: #333;

        .search {
            border-color: gray;
        }
    }

    .sidebar {
        background-color: #222;
        border-color: #333;

        .top {

            .logo {
                color: #999;
            } 
        }
        .line {
            background-color: #333;
        }
        ul {
            li {
                &:hover {
                    background-color: #333;
                }

                .icon {
                    color: #999;
                }
            }
        }
        .bottom {
            .icon {
                color: #999;
            }
        }
    }

    .datatable {
        .datagrid {
          color: gray;
          border: none;
    
          .view,
          .delete,
          .status {
            color: gray;
            border: none;
          }
        }
    }

    input {
        background-color: transparent;
    }
}